* {
  box-sizing: border-box;
}

.app {
  max-width: 415px;
  margin: 0 auto;
  background-color: white;
  border: 1px solid #eee;
  min-height: 100vh;
}

@media (max-width: 415px) {
  .app {
    width: 100%;
  }
}

.brand {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  padding: 20px 0;
}

.nav {
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-evenly;
  list-style: none;
  padding: 10px;
}

.nav-item {
  text-align: center;
  padding: 14px;
  color: #000;
  text-decoration: none;
  border-right: 1px solid #eee;
  font-size: 14px;
}

.nav-item:last-child {
  border-right: none;
}


.section {
  padding: 30px;
}

.section-title {
  margin-top: 0;
}

.section-description {
  line-height: 24px;
}

.articles {
  border-top: 1px solid #eee;
}

.article {
  border-bottom: 1px solid #eee;
  padding: 20px 0;
}

.article-title {
  margin-top: 0;
  color: #000;
}

.article-title, a{
  color: #000;
  text-decoration: none;
}
.article-title, a:hover {
  color: blue;
}

.article-time {
  color: #999;
  font-size: 12px;
}

.article-image {
  margin: 30px 0;
  max-width: 100%;
}

.article-summary {
  line-height: 26px;
  margin-top: 0;
}

.article-source {
  font-size: 12px;
  color: #999;
}